import React, { Component } from "react";
import Form from "react-bootstrap/Form";
import { Link } from "react-router-dom";
export default class FormContainer extends Component {
  render() {
    return (
      <div className="form-container">
        <Form>
          <Form.Group controlId="formBasicEmail">
            <Form.Label>Adresa E-mail</Form.Label>
            <Form.Control type="email" placeholder="E-mail" />
            <Form.Text className="text-muted">text text text</Form.Text>
          </Form.Group>

          <Form.Group controlId="formBasicPassword">
            <Form.Label>Parola</Form.Label>
            <Form.Control type="password" placeholder="password" />
          </Form.Group>

          <Form.Group controlId="formBasicCheckbox">
            <Form.Check custom label="check me out" type="switch" />
          </Form.Group>

          <Form.Group controlId="formBasicTextArea">
            <Form.Label>Sugestii</Form.Label>
            <Form.Control as="textarea" placeholder="orice" rows="4" />
          </Form.Group>
          <button type="submit">
            <Link to="/form/asd" className="pill-1">
              Submit
            </Link>
          </button>
        </Form>
        //dupa submit se poate face redirect spre alte pagini....
      </div>
    );
  }
}
