export const Showcase = () => {
  return (
    <div className="ShowCase">
      <h2 id="herosub">E momentul sa</h2>
      <div className="hr"></div>
      <h1 id="herotitle">facem spitale in romania</h1>
      <p>Nimeni nu se trateaza cu promisiuni</p>
      <div>
        <ShowcaseButton text="Implica-te" caption="text text text">
          <FiThumbsUp />
        </ShowcaseButton>
        <ShowcaseButton text="Spune-ne!" caption="text text text">
          <IoMdChatboxes />
        </ShowcaseButton>
      </div>
    </div>
  );
};
