import React, { Component } from "react";
import profile from "../images/profile.jpg";
import { FaArrowUp, FaArrowRight } from "react-icons/fa";
import { Link } from "react-router-dom";
import image from "../images/pexels2.jpeg";

export default class SingleArticle extends Component {
  render() {
    return (
      <>
        <div className="single-article-container">
          <div className="side-bar">
            <p>
              <Link to="/">
                <FaArrowUp />
              </Link>
            </p>
            <p>
              <a href="#citeste">Citeste</a>
            </p>
            <p>
              <a href="#voteaza">Voteaza</a>
            </p>
            <p>
              <a href="distribuie">Distribuie</a>
            </p>
          </div>
          <div className="single-article-left">
            <h1 className="article-heading" id="citeste">
              [Titlul articolului]
            </h1>
            <div className="meta">
              <p className="article-meta">Categoria Articolului</p>
              <p className="article-meta"> timp citire 7 minute</p>

              <div className="article-meta">
                <p>Numele autorului</p>
                <br />
                <img src={profile} className="profile-pic" />
              </div>
              <p className="article-meta ">Alte Date despre autor</p>
            </div>
          </div>
          <div className="single-article-right">
            <h2 className="article-heading-2">
              Lorem ipsum, dolor sit amet consectetur adipisicing elit. Amet
              distinctio esse velit aut similique?
              <br />
              ...
            </h2>
            <p className="article-text">
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Pariatur
              ullam inventore cupiditate totam quam quos tenetur maxime eaque
              fugit libero itaque eos quibusdam, laboriosam voluptatum iure
              voluptates reiciendis tempore sunt ab optio cum harum soluta
              quaerat sit? Excepturi, assumenda asperiores. Quasi inventore
              tempora nemo similique itaque nostrum praesentium. Repellat, vero?
            </p>
          </div>
          <div className="spacer"></div>
          <div className="single-article-right">
            <p className="article-text">
              Lorem ipsum dolor, sit amet consectetur adipisicing elit. Natus
              perferendis quis dolores cum impedit. Pariatur hic perspiciatis
              esse laboriosam. Consectetur, nostrum distinctio! Cum aperiam
              obcaecati aspernatur cupiditate cumque et officiis nemo vitae
              explicabo. At dolor adipisci sed dolore tenetur maxime tempore
              molestias deleniti alias, impedit eaque provident, repellat culpa,
              consequuntur fugit sint. A repellendus ab neque est dolorum, dicta
              tempora animi hic tenetur possimus et eius ut voluptatibus dolor!
            </p>

            <Link to="/form/asd" className="pill-1">
              Ajuta-ne cu un vot <FaArrowRight />
            </Link>
          </div>
          <div className="single-article-right">
            <img src={image} />
          </div>
        </div>
        <hr />
      </>
    );
  }
}
