import React, { Component } from "react";

export default class FormSituatie extends Component {
  state = {
    orase: [
      {
        nume: "Iasi",
        procentaj: "41%",
        locale: [
          [
            "Ordonanta de functionare",
            "Lorem ipsum dolor sit amet consectetur adipisicing elit. Assumenda, odio!"
          ]
        ],
        finantare: [
          [
            "Arobarea bugetului",
            "Guvernula a aprobat bugetul pentru constructia spitalului regional de la Iasi"
          ],
          [
            "Punct de lucru Pentru Buget",
            "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Officiis quo eaque omnis cumque quis veniam eveniet ipsam praesentium porro commodi."
          ]
        ],
        id: 1
      },
      {
        nume: "Bucuresti",
        procentaj: "10%",
        locale: [
          [
            "Ordonanta de functionare",
            "Lorem ipsum dolor sit amet consectetur adipisicing elit. Assumenda, odio!"
          ]
        ],
        finantare: [
          [
            "Arobarea bugetului",
            "Guvernula a aprobat bugetul pentru constructia spitalului regional de la Iasi"
          ]
        ],
        id: 2
      }
    ],
    selected: 1
  };
  handleChange = e => {
    this.setState({ selected: e.target.value });
  };
  render() {
    const selected = this.state.orase.find(
      item => item.id == this.state.selected
    );
    return (
      <div className="sitform">
        <p className="sith1">SPITALE REGIONALE</p>
        <select value={this.state.selected} onChange={this.handleChange}>
          {this.state.orase.map(item => (
            <option key={item.id} value={item.id}>
              {item.nume}
            </option>
          ))}
        </select>
        <div className="select-result">
          <div id="city-cont">
            <p id="city">{selected.nume}-</p>
            <div id="butwidget">
              <button>Doneaza</button>
              <br />
              <p className="sub">
                Sustine constructia spitalului in orasul tau
              </p>
            </div>
          </div>
          <div className="sit-meta">
            <p>Finantare</p>
            {selected.finantare.map(item => (
              <>
                <h4>{item[0]}</h4>
                <p className="sub">{item[1]}</p>
              </>
            ))}
          </div>
          <div className="sit-meta">
            <p>Locale</p>
            {selected.locale.map(item => (
              <>
                <h4>{item[0]}</h4>
                <p className="sub">{item[1]}</p>
              </>
            ))}
          </div>
          <div className="sit-meta">
            <p>Etapa spital</p>
            <p className="pro">{selected.procentaj}</p>
          </div>
        </div>
      </div>
    );
  }
}
